<template>
	<YPage :logo="require('@/assets/img/logo_white.png')">
		<div class="yu-page-container s-tool-wrapper">
			<div class="s-tool-content" v-for="(item,index) in toolList" :key="index">
				<div class="s-tool-title">{{item.title}}</div>
				<div class="yu-flex-wrap">
					<div class="s-tool-list" v-for="(subItem,subIndex) in item.list" :key="index + '_' + subIndex" @click="handleToolItem(subItem)">
						<div class="s-tool-item yu-flex-level">
							<div class="item-hd">
								<img class="icon" :src="subItem.icon" />
							</div>
							<div class="item-bd yu-flex-item">
								<div class="name">{{subItem.name}}</div>
								<p>{{subItem.desc}}</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</YPage>
</template>
<script>
	import YPage from '@/components/YPage.vue'
	export default {
		components: {
			YPage
		},
		data() {
			return {
				toolList:[
					{
						title:"代码工具",
						list:[
							{
								icon:require('@/assets/logo.png'),
								name:'JSON格式化',
								desc:'这里是介绍，很长很长的介绍',
								url:'/tool/jsonFormatter'
							},
							{
								icon:require('@/assets/logo.png'),
								name:'图片压缩',
								desc:'这里是介绍，很长很长的介绍',
								url:'/tool/image?type=1'
							},
							{
								icon:require('@/assets/logo.png'),
								name:'图片转base64',
								desc:'图片转base64',
								url:'/tool/image?type=2'
							},
              {
                icon:require('@/assets/logo.png'),
                name:'管理计算工具',
                desc:'管理计算工具(自用)',
                url:'/dorm/index'
              }
						]
					},
					{
						title:"在线工具",
						list:[
							{
								icon:'data:image/png;base64,',
								name:'微信截图生成工具',
								desc:'微信朋友圈在线生成工具',
								thirdUrl:'https://ourboy.cn/dz/'
							},
							{
								icon:'data:image/png;base64,',
								name:'代码美化',
								desc:'为你创建漂亮的代码图像，它是一个免费的开源工具，网站上提供了10多种编程语言。',
								thirdUrl:'https://chalk.ist/'
							}
						]
					}
				]
			}
		},
		methods:{
			handleToolItem(obj){
				if(obj.url){
					this.$router.push(obj.url)
				}else if(obj.thirdUrl){
					window.open(obj.thirdUrl,"_blank");
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.s-tool-wrapper{
		padding: 20px 0;
		.s-tool-title{
			padding: 10px 6px;
			font-size: 16px;
			color: #333;
			font-weight: 500;
		}
		.s-tool-list{
			width: 25%;
			.s-tool-item{
				margin: 6px;
				padding: 12px;
				border-radius: 5px;
				background-color: #fff;
				cursor: pointer;
				&:hover{
					background-color: #f5f6f7;
				}
				.item-hd{
					font-size: 0;
					line-height: 0;
					margin-right: 10px;
					.icon{
						width: 40px;
						height: 40px;
					}
				}
				.item-bd{
					width: 0;
					.name{
						font-size: 15px;
						line-height: 24px;
						color: #333;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}
					p{
						font-size: 13px;
						line-height: 16px;
						color: #999;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}
				}
			}
		}
	}
</style>
